<template>
  <modal
    class="x-component-store-box"
    :show="show"
    width="780px"
    :outside-closable="false"
    @close="onCancel">
    <div class="store-box">
      <div class="modal-header">
        <span>选择组件</span>
      </div>

      <div class="component-list-box">
        <scroll-view class="category-box" :suppress-scroll-x="true">
          <div
            class="category"
            v-for="category in categorys"
            :class="{selected: currCategory === category}"
            @click="onSelectCategry(category)"
            >
            <span>{{category.name}}</span>
          </div>
        </scroll-view>

        <scroll-view class="component-list clearfix">
          <div class="component"
            v-for="component in currComponentList"
            :class="{selected: component === selectedComponent}"
            @click="selectedComponent = component"
            >
              <div class="component-msg">
                <div class="component-img">
                  <img :src="component.img" alt="">
                </div>
                <div class="component-name">
                  <span>{{component.name}}</span>
                </div>
                <div class="component-describe">
                  <span>{{component.describe}}</span>
                </div>
              </div>
          </div>

        </scroll-view>
      </div>

      <div class="modal-footer">
        <x-button @click.native="onCancel">取消</x-button>
        <x-button type="success" @click.native="onConfim">确定</x-button>
      </div>

    </div>
  </modal>
</template>

<script>
import groupBy from 'lodash/groupBy'
import {COMPONENTS, CATEGORYS} from '../data/components'
export default {
  name: 'ComponentsModal',

  props: {
    show: {
      default: false
    },
    selected: {
      default () {
        return {}
      }
    }
  },

  data () {
    return {
      defaultComponents: COMPONENTS,
      selectedCategoryId: 'all',
      selectedComponent: {}
    }
  },

  computed: {
    categorys () {
      let result = [{name: '全部', id: 'all'}]
      result = result.concat(CATEGORYS)
      return result
    },
    currCategory () {
      let result = {}
      this.categorys.forEach((item) => {
        if (item.id === this.selectedCategoryId) {
          result = item
        }
      })
      return result
    },

    components () {
      let result = {}
      result = groupBy(this.defaultComponents, (item) => {
        return item.category.id
      })
      result['all'] = this.defaultComponents
      return result
    },

    currComponentList () {
      return this.components[this.currCategory.id]
    }
  },

  mounted () {
    this.init()
  },

  methods: {
    init () {
      this.selectedComponent = this.selected
    },
    onConfim () {
      this.$emit('confim', this.selectedComponent)
    },

    onCancel () {
      this.$emit('cancel')
    },

    onSelectCategry (category) {
      this.selectedCategoryId = category.id
    }
  }
}
</script>

<style lang="stylus" scoped>

.x-component-store-box
  .store-box
    width 100%
    height auto
    padding 20px 20px 30px
    box-sizing border-box
    overflow hidden

    .modal-header
      height 30px
      line-height 30px
      font-size 16px

    .component-list-box
      position relative
      margin-bottom 20px
      width 100%
      height 450px
      background #eee
      padding-left 185px
      padding-right 10px
      box-sizing border-box


      .category-box
        position absolute
        top 0
        left 0
        width 180px
        height 100%
        background #eee
        padding 10px
        box-sizing border-box
        border-right 1px solid #ddd
        .category
          position relative
          width 100%
          height 60px
          line-height 60px
          background #eee
          padding-left 10px
          box-sizing border-box
          transition all ease 0.2s
          cursor pointer
          &.selected
            background #fff
          &:hover
            background #e0e0e0
            padding-left 15px

      .component-list
        width 100%
        height 100%
        padding 10px
        box-sizing border-box
        .component
          float left
          padding 0 5px
          margin-bottom 10px
          box-sizing border-box
          width 50%
          height 100px
          cursor pointer
          &.selected
            box-shadow 0 0 3px 3px rgba(0,0,0,0.08)
          &:hover
            background #e5e5e5

          .component-msg
            position relative
            width 100%
            height 100%
            padding 20px 10px
            padding-left 90px
            box-sizing border-box
            .component-img
              position absolute
              left 10px
              top 20px
              width 60px
              height 60px
              background #bbb
            .component-name
              height 25px
              font-size 14px
              font-weight bold
            .component-describe
              font-size 12px

  .modal-footer
    margin-top 10px
    text-align right
</style>



