<template>
  <modal
    class="layout-config-box"
    :show="show"
    width="780px"
    :outside-closable="false"
    @close="onCancel">
    <div class="layouts-box">
      <div class="modal-header">
        <span>选择布局</span>
      </div>
      <div class="layout-list">
        <div
          class="layout"
          v-for="(layout, key) in defaultLayouts"
          :class="{selected: selectedLayout === key}"
          @click="selectedLayout = key">
          <div class="icon layout-icon">
            <x-row>
              <x-col
                class="layout-col"
                v-for="(colCount, index) in getLayoutColCounts(key)"
                key="index"
                :span="colCount - 0">
                <div class="icon-modular-box">
                </div>
              </x-col>
            </x-row>
          </div>
          <p class="name">{{layout.label}}</p>
        </div>
      </div>
      <div class="modal-footer">
        <x-button @click.native="onCancel">取消</x-button>
        <x-button type="success" @click.native="onConfim">确定</x-button>
      </div>
    </div>
  </modal>
</template>

<script>
import LAYOUTS from '../data/layouts'
export default {
  name: 'LayoutModal',

  props: {
    show: {
      default: false
    },
    selected: {
      default () {
        return '24'
      }
    }
  },

  data () {
    return {
      defaultLayouts: LAYOUTS,
      selectedLayout: ''
    }
  },

  mounted () {
    this.init()
  },

  methods: {
    init () {
      this.selectedLayout = this.selected
    },
    onConfim () {
      this.$emit('confim', this.selectedLayout)
    },

    onCancel () {
      this.$emit('cancel')
    },

    getLayoutColCounts (layout) {
      let counts = layout.split(':')
      return counts
    }
  }
}
</script>

<style lang="stylus" scoped>
.layout-config-box
  .layouts-box
    width 100%
    height auto
    padding 20px 20px 30px
    box-sizing border-box
    overflow hidden

  .modal-header
    height 30px
    line-height 30px
    font-size 16px

  .layout-list
    width 100%
    height 400px
    margin-top 30px

    .layout
      display inline-block
      min-width 150px
      width 177px
      height 200px
      margin-right 10px
      margin-bottom 10px
      overflow hidden
      text-align center
      transition all ease 0.3s
      &:nth-child(4n)
        margin-right 0
      &.selected
        box-shadow 1px 1px 5px 3px rgba(0,0,0,0.1)

      .icon
        display block
        margin 25px auto 0
        width 120px
        height 120px
        background #aaa
        border 1px solid #aaa

        .icon-modular-box
          width 100%
          height 120px
          background #fff
          border 1px solid #aaa
          box-sizing border-box

  .modal-footer
    margin-top 10px
    text-align right
</style>



