<template>
  <div class="x-add-btn-root">
    <icon type="plus"></icon>
    <span>{{label}}</span>
  </div>
</template>

<script>
export default {
  name: 'AddBtn',

  props: {
    label: {
      default: ''
    }
  }
}
</script>

<style lang="stylus">
  .x-add-btn-root
    width 100%
    height 30px
    line-height 30px
    text-align center
    overflow hidden
    border 1px dashed #bbb
    color #aaa
    border-radius 3px
    cursor pointer
    transition all ease 0.3s

    &:hover
      background rgba(0,0,0,0.03)
      border 1px dashed #999
      color #888
</style>



