<template>
  <scroll-view class="edit-page">
    <div class="content">

      <transition-group name="list-complete" tag="div">
        <div class="layout-row"
          v-for="(layoutMessage, rowIndex) in layoutMessages"
          :key="rowIndex">
          <x-row :gutter="5">
            <x-col
              class="layout-col"
              v-for="(colCount, colIndex) in getLayoutColCounts(layoutMessage.layout)"
              key="colIndex"
              :span="colCount - 0">
              <div class="modular-box">
                <div class="component-box" v-for="(component, componentIndex) in layoutMessage.components[colIndex]">
                  <component :is="getComponentName(component.id)"></component>
                </div>
                <add-btn
                  class="add-component-btn"
                  label="添加组件"
                  @click.native="onShowAddComponents(layoutMessage.components[colIndex])">
                </add-btn>
              </div>
            </x-col>
          </x-row>

          <template>
            <div class="border border-top"></div>
            <div class="border border-bottom"></div>
            <div class="border border-left"></div>
            <div class="border border-right"></div>
            <div class="edit-layout-row">
              <button-group size="mini">
                <x-button
                  :disabled="rowIndex === 0"
                  @click.native="onLayoutMoveUp(layoutMessage, rowIndex)">
                  上移
                </x-button>
                <x-button
                  :disabled="rowIndex === layoutMessages.length - 1"
                  @click.native="onLayoutMoveDown(layoutMessage, rowIndex)">
                  下移
                </x-button>
                <x-button @click.native="onLayoutAdd(rowIndex + 1)">添加</x-button>
                <x-button @click.native="onLayoutEdit(layoutMessage, rowIndex)">编辑</x-button>
                <x-button @click.native="onLayoutDelete(layoutMessage, rowIndex)">删除</x-button>
              </button-group>
            </div>
          </template>
        </div>
      </transition-group>

      <add-btn label="添加布局" @click.native="onShowAddLayoutModal(layoutMessages.length)"></add-btn>
    </div>

    <layout-modal
      :show="layoutModal.show"
      :selected="layoutModal.selectedLayout"
      @cancel="layoutModal.show = false"
      @confim="onLayoutModalConfim"
      >
    </layout-modal>

    <component-modal
      :show="componentModal.show"
      @cancel="componentModal.show = false"
      @confim="onComponentsModalConfim"
      >
    </component-modal>
  </scroll-view>
</template>

<script>
import LayoutModal from './layout-modal'
import ComponentModal from './component-modal'
import AddBtn from './add-btn'
import {COMPONENTS} from '../data/components'
import componentStore from '../components/store'

export default {
  name: 'EditPage',

  components: {
    AddBtn,
    LayoutModal,
    ComponentModal,
    ...componentStore
  },

  data () {
    return {
      layoutModal: {
        show: false,
        selectedLayout: '24',
        editing: false
      },
      layoutInsertPosition: -1,
      editingLayoutMessage: {},

      layoutMessages: [
        // {
        //   layout: '8:8:8',
        //   components: [
        //     [
        //       {
        //         id: '1'
        //       },
        //       {
        //         id: '2'
        //       }
        //     ],
        //     [],
        //     []
        //   ]
        // }
      ],

      componentModal: {
        show: false,
        selectedComponent: {},
        editing: false
      },

      editingComponentList: {}
    }
  },

  computed: {
  },

  methods: {
    getLayoutColCounts (layout) {
      let counts = layout.split(':')
      return counts
    },
    onShowAddLayoutModal (layoutInsertPosition) {
      this.layoutInsertPosition = layoutInsertPosition
      this.layoutModal.editing = false
      this.layoutModal.show = true
    },

    onLayoutModalConfim (selectedLayout) {
      this.layoutModal.selectedLayout = selectedLayout
      this.layoutModal.editing ? this.onConfimEditLayout() : this.onConfimAddLayout()
    },

    onConfimAddLayout () {
      let count = this.layoutModal.selectedLayout.split(':').length
      let defaultComponents = []
      for (var i = 0; i < count; i++) {
        defaultComponents.push([])
      }
      let newLayout = {
        layout: this.layoutModal.selectedLayout,
        components: defaultComponents
      }
      this.addLayout(newLayout, this.layoutInsertPosition)
      this.layoutModal.show = false
    },

    addLayout (leyout, position) {
      this.layoutMessages.splice(position, 0, leyout)
    },

    onConfimEditLayout () {
      this.editingLayoutMessage.layout = this.layoutModal.selectedLayout
      this.layoutModal.show = false
    },

    onLayoutMoveUp (layoutMessage, index) {
      this.layoutMessages.splice(index, 1)
      this.layoutMessages.splice(index - 1, 0, layoutMessage)
    },
    onLayoutMoveDown (layoutMessage, index) {
      this.layoutMessages.splice(index, 1)
      this.layoutMessages.splice(index + 1, 0, layoutMessage)
    },
    onLayoutAdd (layoutInsertPosition) {
      this.onShowAddLayoutModal(layoutInsertPosition)
    },
    onLayoutEdit (layoutMessage, index) {
      this.layoutModal.selectedLayout = layoutMessage.layout
      this.layoutModal.editing = true
      this.editingLayoutMessage = layoutMessage
      this.layoutModal.show = true
    },
    onLayoutDelete (layoutMessage, index) {
      this.layoutMessages.splice(index, 1)
    },

    onShowAddComponents (components) {
      this.editingComponentList = components
      this.componentModal.editing = false
      this.componentModal.show = true
      // 这里显示添加组件浮层
    },

    getComponentName (id) {
      let result = ''
      COMPONENTS.forEach((item) => {
        if (item.id === id) {
          result = item.componentName
        }
      })
      return result
    },

    getComponents (components, index) {
      return components[index]
    },

    onComponentsModalConfim (component) {
      if (this.componentModal.editing) {
        // 编辑组件
      } else {
        // 添加组件
        this.editingComponentList.push(component)
        this.componentModal.show = false
      }
    }
  }
}
</script>

<style lang="stylus" scoped>

.edit-page
  width 100%
  height 100%
  padding 10px
  box-sizing border-box
  .content
    width 100%
    height auto

  .layout-row
    position relative
    margin-bottom 10px
    box-sizing border-box

    .layout-col
      .modular-box
        border 1px dashed #777
        min-height 200px
        padding 10px
        box-sizing border-box
        &:hover .add-component-btn
          opacity 1
        .add-component-btn
          opacity 0
        .component-box
          margin-bottom 10px
    &:hover .border
      display block
    &:hover .edit-layout-row
      display block
    .border
      position absolute
      background red
      display none
    .border-top
      top 0
      left 0
      width 100%
      height 1px
    .border-bottom
      bottom 0
      left 0
      width 100%
      height 1px
    .border-left
      left 0
      top 0
      width 1px
      height 100%
    .border-right
      right 0
      top 0
      width 1px
      height 100%
    .edit-layout-row
      display none
      position absolute
      right 1px
      bottom 1px

.layout-row
  transition all 0.3s
.list-complete-enter, .list-complete-leave-to
  opacity 0
  transform scaleY(0) translateY(-30px)
  transform-origin top
.list-complete-leave-active
  /*position absolute*/

</style>



